$myHeight: 80vh;
$offcanvas-width: 40%;

.box {

    .logo {
        height: 3rem;
        width: auto;
    }

    .title-list {
        max-height: $myHeight;
        height: $myHeight;
        overflow-y: scroll;

        li {
            background: none;
            transition: background-color .1s;

            &:hover {
                background-color: #0066CC;
                cursor: pointer;
            }
        }

    }

    .offcanvas {
        width: $offcanvas-width !important;
    }

    object {
        height: $myHeight;
    }

    .goTopBtn {
        position: fixed !important;
        bottom: 1rem;
        right: 1rem;
    }

}